.menu {
  position: fixed;
  inset: 0;
  background-color: $color-red;
  opacity: 0;

  pointer-events: none;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
  transform: translateX(-100%);

  &:target {
    pointer-events: all;
    opacity: 1;
    z-index: 3;

    transform: translateX(0);
    overflow: clip;
  }
}
