.header {
  width: 100%;
  height: 100vh;

  gap: 120px;
  background: url('./imagec/header/header-bg--x2.png') no-repeat;
  background-size: cover;
  background-position: center;

  padding-top: 50px;

  &--grid {
    @include page-grid;
    @include pading-inlain;

    color: $color-white;

    box-sizing: border-box;
  }

  &__title {
    &--item {
      font-weight: 700;
      font-size: 40px;
      line-height: 100%;
      letter-spacing: -1.5px;
      text-align: center;
      vertical-align: middle;
    }

    padding-top: 50px;
    grid-column: span 2;

    @include on-tablet {
      grid-column: 2 / 6;
      &--item {
        font-style: Bold;
        font-size: 80px;

        line-height: 80px;
        letter-spacing: -1.5px;
        text-align: center;
        vertical-align: middle;
      }
    }

    @include on-pc {
      grid-column: 4 / 10;
      &--item {
        font-style: Bold;
        font-size: 80px;

        line-height: 80px;
        letter-spacing: -1.5px;
        text-align: center;
        vertical-align: middle;
      }
    }
  }

  &__top {
    justify-content: center;
    align-items: center;
    grid-column: span 2;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-pc {
      grid-column: span 12;
    }

    @include page-grid;

    &__contact {
      position: absolute;
      bottom: -50px;

      right: 100%;
      display: none;
      padding: 0;
      width: 220px;
      list-style: none;
      text-align: end;

      &--item {
        @include typografy-important-information;

        text-transform: $text-up-case;
      }

      &--link {
        @include typografy-important-information;

        text-transform: $text-up-case;
        color: $color-white;

        text-decoration: none;
      }
    }

    &--logo {
      grid-column: 1 / 2;
      width: 87px;
      height: 65px;
      justify-content: center;
      align-items: center;

      font-weight: 700;
      letter-spacing: -1.5px;
      font-size: 40px;
      line-height: 80%;
    }

    &__list {
      position: relative;
      grid-column: -1;
      display: flex;
      gap: 55px;
      margin: 0;
      padding: 0;
      justify-content: center;
      align-items: center;
      list-style: none;

      &--menu {
        background: url('./imagec/header/header__top--burger.png') no-repeat;
        background-position: center;

        &:hover {
          background: url('./imagec/header/header__top--burger-active.png')
            no-repeat;
          background-position: center;
          cursor: pointer;
          transition: background 0.3s ease-in-out;
          background-size: 24px 16px;
        }
      }

      &--phone {
        background: url('./imagec/header/header__top--phone.png') no-repeat;
        background-position: center;
      }
    }
  }
}

.logo--link {
  text-decoration: none;
  color: $color-white;
}

.header__top__list--phone:hover > .header__top__contact {
  display: initial;
}
