.nav {
  margin-top: 7px;
  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin: 0;
    padding: 0;
    list-style: none;

    &--item {
      font-weight: 700;
      font-size: 24px;
      line-height: 32px;
      color: $color-white;

      &:hover {
        color: $color-white;
      }
    }
    &--link {
      position: relative;
      line-height: 22px;
      letter-spacing: 2px;
      font-size: 22px;
      text-decoration: none;
      color: $color-white;
      scroll-behavior: smooth;

      &::after {
        content: '';
        position: absolute;
        bottom: -8px;
        display: block;
        height: 1px;
        width: 100%;
        background-color: $color-white;
        transition: 0.3s;
        transform: scale(0);
      }
      &:hover::after {
        transform: scale(1);
      }
    }
  }
}
